import React , {Component} from 'react'

class Form extends Component{
    constructor(props){
        super(props)
        this.initialState = {
            name:'',
            job:''
        }
        this.state = this.initialState
    }
    handleChange = event => {
        const {name, value} = event.target //track the name and value in the input field

        this.setState(
            {
                [name]:value
            }
        )
    }
    submitForm = () => {
        this.props.handleSubmit(this.state) //call the handleSubmit function passed by props
        this.setState(this.initialState)
    }
    render(){
        const {name, job} = this.state
        return(
            <form>
                <label>Name</label>
                <input type='text' name='name' value ={name} onChange={this.handleChange}></input>
                <label>Job</label>
                <input type='text' name='job' value ={job} onChange={this.handleChange}></input>
                <input type='button' value='Submit' onClick = {this.submitForm}></input>
            </form>
        )
    }
}
export default Form; 
