import React, {Component} from 'react';
import Table from './Table.js';
import Form from './Form.js';

class App extends Component {
  state = {
    characters:[]
  }
  removeCharacter = (index) => {
    const {characters} = this.state
    this.setState(
      {
        characters: characters.filter((row, i) => {
        return i !== index})
      } // setting a new object for this state
    )
  }
  handleSubmit = character => {
    this.setState(
      {
        characters:[...this.state.characters, character]
      }
    )
  }
    render() {
      const {characters} = this.state // shorthand to create a variable that contains this.state.characters
      
      return (
        <div className="container">
          <Table testData={characters} removeCharacter = {this.removeCharacter}/>
          <Form handleSubmit = {this.handleSubmit}/>
        </div>
      )
    }
  }
  
  export default App